<template>
  <div>
    <a-form class="ant-advanced-search-form" :form="form" @submit="handleSearch">
      <a-row :gutter="24">
        <a-col :span="6">
          <a-form-item label="咨询编号">
            <a-input v-decorator="['id']" />
          </a-form-item>
        </a-col>
        <a-col :span="6">
          <a-form-item label="咨询标题">
            <a-input v-decorator="['title']" />
          </a-form-item>
        </a-col>
        <a-col :span="6">
          <a-form-item label="地址">
            <a-cascader :fieldNames="{ label: 'name', value: 'name', children: 'children' }"
                        :options="area"
                        v-decorator="['selectedAreas']"
                        placeholder="请选择" @change="onChange" 
            />
          </a-form-item>
        </a-col>
        <a-col :span="6">
          <a-form-item label="咨询人姓名">
            <a-input v-decorator="['name']"/>
          </a-form-item>
        </a-col>
        <a-col :span="6">
          <a-form-item label="状态筛选">
            <a-select allow-clear placeholder="请选择" style="width: 180px" v-decorator="['status']">
              <a-select-option value="0">待处理</a-select-option>
              <a-select-option value="1">已完成</a-select-option>
            </a-select>
          </a-form-item>
        </a-col>
      </a-row>
      <a-row>
        <a-col :span="24" :style="{ textAlign: 'right' }">
          <a-button type="primary" html-type="submit">
            查询
          </a-button>
          <a-button :style="{ marginLeft: '8px' }" @click="handleReset">
            重置
          </a-button>
        </a-col>
      </a-row>
    </a-form>
    <a-button type="danger">删除</a-button>
    
    <div style="margin-top: 2%;"></div>
    
    <a-table :columns="columns"
             :data-source="data"
             :row-selection="rowSelection"
    >
      <span slot="id" slot-scope="text">{{ text }}</span>
      <span slot="customTitle">咨询编号</span>
      <span slot="status" slot-scope="status">
        <a-badge :color="status===0?'blue':'green'" :text="status===0?'待处理':'已完成'"></a-badge>
      </span>
      <span slot="action" slot-scope="text, record">
      <a>回复</a>
      <a-divider type="vertical"/>
      <a>删除</a>
    </span>
    </a-table>
  </div>
</template>
<script>
  import area from "@/views/area";
  
  const columns = [
    {
      dataIndex: 'id',
      key: 'id',
      slots: {title: 'customTitle'},
      scopedSlots: {customRender: 'id'},
    },
    {
      title: '咨询标题',
      dataIndex: 'title',
      key: 'title',
    },
    {
      title: '咨询人姓名',
      dataIndex: 'name',
      key: 'name',
    },
    {
      title: '咨询时间',
      dataIndex: 'date',
      key: 'date',
    },
    {
      title: '地址',
      dataIndex: 'address',
      key: 'address',
    },
    {
      title: '状态',
      key: 'status',
      dataIndex: 'status',
      scopedSlots: {customRender: 'status'},
    },
    {
      title: '操作',
      key: 'action',
      scopedSlots: {customRender: 'action'},
    },
  ];

  const data = [
    {
      key: '1',
      id: '10001',
      title: '标题1',
      address: '浙江省杭州市',
      status: 0,
      name: '张三',
      date: '2021-02-28'
    },
    {
      key: '2',
      id: '10002',
      title: '标题2',
      address: '浙江省杭州市',
      status: 1,
      name: '张三',
      date: '2021-02-28'
    },
    {
      key: '3',
      id: '10003',
      title: '标题3',
      address: '浙江省杭州市',
      status: 1,
      name: '张三',
      date: '2021-02-28'
    },
  ];


  const rowSelection = {
    onChange: (selectedRowKeys, selectedRows) => {
      console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
    },
    onSelect: (record, selected, selectedRows) => {
      console.log(record, selected, selectedRows);
    },
    onSelectAll: (selected, selectedRows, changeRows) => {
      console.log(selected, selectedRows, changeRows);
    },
  };

  export default {
    name: 'Consults',
    data() {
      return {
        data,
        columns,
        rowSelection,
        expand: false,
        form: this.$form.createForm(this, {name: 'advanced_search'}),
        area,
      };
    },
    methods: {
      handleSearch(e) {
        e.preventDefault();

        const values = this.form.getFieldsValue();
        console.log(values);
      },

      handleReset() {
        this.form.resetFields();
      },

      toggle() {
        this.expand = !this.expand;
      },

      onChange(value) {
        // console.log(value);
      },
    },
  };
</script>

<style scoped>
  .ant-advanced-search-form {
    padding: 24px;
    /*background: #fbfbfb;
    border: 1px solid #d9d9d9;
    border-radius: 6px;*/
  }

  .ant-advanced-search-form .ant-form-item {
    display: flex;
  }

  .ant-advanced-search-form .ant-form-item-control-wrapper {
    flex: 1;
  }

  #components-form-demo-advanced-search .ant-form {
    max-width: none;
  }
</style>
